import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { CartHeadAction, TableData, ColEditDelete } from 'components/common';
import { BtnAddNew } from 'components/common/button';
import { Card, Row, Col, Checkbox } from 'antd';
import { moneyToString } from 'utils/formatUtil';
import { getAccountsReceivable } from 'services/accountsReceivable';

class AccountsReceivable extends Component {
  state = {};

  handleDelete = id => {
    console.log(id);
  };

  render() {
    const columns = [
      {
        title: 'STT',
        dataIndex: 'index',
        key: 'index',
        width: '100px',
      },
      {
        title: 'Tên khoản thu',
        dataIndex: 'name',
        key: 'name',
      },
      {
        title: 'Học phí',
        dataIndex: 'type1',
        render: (text, record) => (
          <Checkbox defaultChecked={record.type1} disabled />
        ),
      },
      {
        title: 'Kinh phí ĐT',
        dataIndex: 'type2',
        render: (text, record) => (
          <Checkbox defaultChecked={record.type2} disabled />
        ),
      },
      {
        title: 'Học lại',
        dataIndex: 'type3',
        render: (text, record) => (
          <Checkbox defaultChecked={record.type3} disabled />
        ),
      },
      {
        title: 'Thi lại',
        dataIndex: 'type4',
        render: (text, record) => (
          <Checkbox defaultChecked={record.type4} disabled />
        ),
      },
      {
        title: 'Số tiền',
        dataIndex: 'price',
        render: text => moneyToString(text),
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#accounts-receivable/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh mục khoản thu">
        <CartHeadAction>
          <BtnAddNew href="#accounts-receivable/create" />
        </CartHeadAction>
        <Row>
          <Col span={24}>
            <TableData
              columns={columns}
              service={getAccountsReceivable}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <AccountsReceivable />
  </Page>
);


